.todo-app {
	display: flex;
	flex-direction: column;
	justify-content: start;
	width: 520px;
	min-height: 600px;
	background: #161a2b;
	text-align: center;
	margin: 128px auto;
	border-radius: 10px;
	padding-bottom: 32px;
}

h1 {
	text-align: center;
	margin: 32px 0;
	color: black;
	font-size: 24px;
}

.complete {
	text-decoration: line-through;
	opacity: 0.4;
}

.todo-form {
	margin-bottom: 32px;
	text-align: center;
}

.todo-input {
	padding: 12px 32px 12px 16px;
	border-radius: 4px 0 0 4px;
	border: 2px solid deepskyblue;
	outline: none;
	width: 600px;
	background: transparent;
	color: #464242;
	font-size: 20px;
}

.todo-input::placeholder {
	color: rgba(3, 3, 3, 0.55);
	font-size: 20px ;
}

.todo-button {
	padding: 14px 32px 14px 16px;
	border: none;
	border-radius: 0 4px 4px 0;
	cursor: pointer;
	outline: none;
	background: deepskyblue;
	color: #332c2c;
	text-transform: capitalize;
	font-size: 20px;
}

.todo-input.edit {
	border: 2px solid black;
	padding: 14px 32px 14px 16px;
}

.todo-button.edit {
	background: linear-gradient(
		90deg,
		rgba(20, 159, 255, 1) 0%,
		rgba(17, 122, 255, 1) 100%
	);
	padding: 16px 22px;
}

.todo-container {
	display: flex;
	flex-direction: row;
	position: relative;
}

.todo-row {
	cursor: pointer;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: 4px auto;
	color: #090808;
	background: rgba(247, 45, 255, 0.7);
	/*border: 1px solid #090808;*/
	padding: 16px;
	border-radius: 5px;
	width: 90%;
}

.todo-row:nth-child(4n + 1) {
	background: rgba(231, 231, 76, 0.7);
}

.todo-row:nth-child(4n + 2) {
	background: rgba(255, 128, 128, 0.7);
}

.todo-row:nth-child(4n + 3) {
	background: rgba(20, 159, 255, 0.7)


}

.icons {
	display: flex;
	align-items: center;
	font-size: 24px;
	cursor: pointer;
}

.delete-icon {
	margin-right: 5px;
	color: black;
}

.edit-icon {
	color: black;
}
